.menuContainer .menuBLD {
  display: table;
  padding: 10px; }
  .menuContainer .menuBLD > div {
    display: table-cell; }
  .menuContainer .menuBLD > div:not(:last-of-type) {
    padding-right: 20px; }
  .menuContainer .menuBLD h3 {
    margin: 10px 0; }
    .menuContainer .menuBLD h3 .title {
      font-weight: 700; }
  .menuContainer .menuBLD .menuList {
    min-width: 100px;
    line-height: 1.2;
    display: table; }
    @media screen and (orientation: portrait) and (max-width: 440px) {
      .menuContainer .menuBLD .menuList {
        display: flex;
        flex-direction: column; }
        .menuContainer .menuBLD .menuList > div[data-list]:not(:last-of-type) {
          padding-right: 0 !important;
          padding-bottom: 15px; } }
    .menuContainer .menuBLD .menuList > div:not([data-list]) {
      margin-bottom: 5px; }
    .menuContainer .menuBLD .menuList > div[data-list] {
      display: table-cell;
      max-width: 185px; }
      @media screen and (orientation: portrait) and (max-width: 440px) {
        .menuContainer .menuBLD .menuList > div[data-list] {
          max-width: 165px; } }
      .menuContainer .menuBLD .menuList > div[data-list]:not(:last-of-type) {
        padding-right: 15px; }
      .menuContainer .menuBLD .menuList > div[data-list] h4 {
        margin: 0 0 5px 0; }
        .menuContainer .menuBLD .menuList > div[data-list] h4 .subTitle {
          font-weight: 700;
          color: #ffc206; }
      .menuContainer .menuBLD .menuList > div[data-list] p {
        margin: .8em 0; }
        .menuContainer .menuBLD .menuList > div[data-list] p:first-child {
          margin-top: 0; }
